import type { PerspectiveCamera, OrthographicCamera } from 'three';
import { Evented } from './evented';
import { PlanarCameraHelper } from '../helpers/planarCameraHelper';
import type { ICameraHelper, EaseOptions, FlyToOptions } from '../helpers/icameraHelper';
import { ThreePlanarTransform } from '../transform/threePlanarTransform';
import type { ITransform, Padding, TransformConstraints, Bounds2D, MethodOptions, Center } from '../transform/interfaces';
import { browser, raf, caf } from '../util/browser';
import { defaultEasing } from '../util/easing';
import { HandlerManager, type HandlerManagerOptions } from '../handlers/handlerManager';
import type { HandlerDelta } from '../handlers/types';
import { computeFlyParams, uAt, widthAt } from '../util/flight';

export type Projection = 'planar'; // future: 'spherical'

export interface CameraControllerOptions {
  camera: PerspectiveCamera | OrthographicCamera;
  domElement: HTMLElement;
  width?: number;
  height?: number;
  devicePixelRatio?: number;

  // Coordinate system & projection
  upAxis?: 'y' | 'z'; // default 'y' (Three.js standard), 'z' for GIS-style
  projection?: Projection | import('../transform/interfaces').ProjectionAdapter;
  baseScale?: number; // Base scale factor for coordinate conversion

  // Behavior
  bearingSnap?: number;
  bearingSnapEpsilon?: number;
  handlers?: HandlerManagerOptions;

  // Constraints
  minZoom?: number;
  maxZoom?: number;
  minPitch?: number;
  maxPitch?: number;
  panBounds?: Bounds2D;

  // Event control
  suppressEvents?: boolean; // Globally suppress events

  // Animation loop control
  useExternalAnimationLoop?: boolean; // Don't start internal loop, user calls update()
  observeResize?: boolean; // Auto-setup ResizeObserver
}

export type CameraMoveEvents = {
  movestart: { originalEvent?: Event };
  move: { originalEvent?: Event };
  moveend: { originalEvent?: Event };
  zoomstart: { originalEvent?: Event };
  zoom: { originalEvent?: Event };
  zoomend: { originalEvent?: Event };
  rotatestart: { originalEvent?: Event };
  rotate: { originalEvent?: Event };
  rotateend: { originalEvent?: Event };
  pitchstart: { originalEvent?: Event };
  pitch: { originalEvent?: Event };
  pitchend: { originalEvent?: Event };
  rollstart: { originalEvent?: Event };
  roll: { originalEvent?: Event };
  rollend: { originalEvent?: Event };
  dragstart: { originalEvent?: Event };
  drag: { originalEvent?: Event };
  dragend: { originalEvent?: Event };
  renderFrame: {};
  error: { error: Error };
};

// Animation state for external loop support
type AnimationState = {
  type: 'ease' | 'fly';
  t0: number;
  duration: number;
  easing: (t: number) => number;
  start: {
    center: Center;
    zoom: number;
    bearing: number;
    pitch: number;
    roll: number;
    padding: Padding;
  };
  target: {
    center: Center;
    zoom: number;
    bearing: number;
    pitch: number;
    roll: number;
    padding: Padding;
  };
  axes: {
    zoom: boolean;
    rotate: boolean;
    pitch: boolean;
    roll: boolean;
    pan: boolean;
  };
  // easeTo-specific
  anchorPt?: { x: number; y: number };
  anchorTightness?: number;
  // flyTo-specific
  flyParams?: {
    useScreenSpeed: boolean;
    screenSpeed?: number;
    S: number;
    w0: number;
    w1: number;
    u1: number;
    rho: number;
    params: any;
    dirX: number;
    dirY: number;
    traveled?: number;
    last?: number;
    startCenter: Center;
    endCenter: Center;
    startZoom: number;
    endZoom: number;
    startBearing: number;
    endBearing: number;
    startPitch: number;
    endPitch: number;
    startRoll: number;
    endRoll: number;
    worldDist: number;
  };
};

export class CameraController extends Evented<CameraMoveEvents> {
  private readonly _camera: PerspectiveCamera | OrthographicCamera;
  private readonly _dom: HTMLElement;
  private readonly _helper: ICameraHelper;
  readonly transform: ITransform;
  private _moving = false;
  private _animHandle: number | null = null;
  private _easeAbort?: AbortController;
  private _bearingSnap: number;
  private _bearingSnapEps: number = 0.001;
  private _handlers?: HandlerManager;
  private _moveEndTimer: number | null = null;
  private _zooming = false;
  private _rotating = false;
  private _pitching = false;
  private _rolling = false;
  private _dragging = false;
  private _constraints: TransformConstraints = { minZoom: -Infinity, maxZoom: Infinity, minPitch: 0, maxPitch: 85 };
  private _softClamping = false;
  private _suppressEvents: boolean = false;
  private _isInternalUpdate: boolean = false;
  private _resizeObserver?: ResizeObserver;
  private _useExternalLoop: boolean = false;
  private _activeAnimation: AnimationState | null = null;

  constructor(opts: CameraControllerOptions) {
    super();
    if (typeof window === 'undefined') {
      // SSR guard: don't access DOM
      // @ts-ignore
      this._camera = opts.camera;
      // @ts-ignore
      this._dom = {} as any;
      this._helper = new PlanarCameraHelper();
      this.transform = new ThreePlanarTransform({
        camera: opts.camera,
        width: opts.width ?? 0,
        height: opts.height ?? 0,
        devicePixelRatio: opts.devicePixelRatio,
        upAxis: opts.upAxis,
        projection: typeof opts.projection === 'object' ? opts.projection : undefined,
        baseScale: opts.baseScale,
      });
      this._bearingSnap = opts.bearingSnap ?? 7;
      this._suppressEvents = opts.suppressEvents ?? false;
      this._useExternalLoop = opts.useExternalAnimationLoop ?? false;
      return;
    }

    this._camera = opts.camera;
    this._dom = opts.domElement;
    this._helper = new PlanarCameraHelper();
    this._suppressEvents = opts.suppressEvents ?? false;
    this._useExternalLoop = opts.useExternalAnimationLoop ?? false;

    this.transform = new ThreePlanarTransform({
      camera: opts.camera,
      width: opts.width ?? this._dom.clientWidth,
      height: opts.height ?? this._dom.clientHeight,
      devicePixelRatio: opts.devicePixelRatio,
      upAxis: opts.upAxis,
      projection: typeof opts.projection === 'object' ? opts.projection : undefined,
      baseScale: opts.baseScale,
    });
    this._bearingSnap = opts.bearingSnap ?? 7;
    this._bearingSnapEps = opts.bearingSnapEpsilon ?? 0.001;
    // Constraints
    this._constraints = {
      minZoom: opts.minZoom ?? -Infinity,
      maxZoom: opts.maxZoom ?? Infinity,
      minPitch: opts.minPitch ?? 0,
      maxPitch: opts.maxPitch ?? 85,
      panBounds: opts.panBounds,
    };
    this.transform.setConstraints(this._constraints);

    // Handlers: pass through all provided handler options and ensure onChange is wired.
    const handlerOptions: HandlerManagerOptions = {
      ...(opts.handlers ?? {}),
      scrollZoom: opts.handlers?.scrollZoom ?? { around: 'center' },
      onChange: (delta: HandlerDelta) => this._externalChange(delta),
    } as any;
    this._handlers = new HandlerManager(this._dom, this.transform, this._helper, handlerOptions);

    // Setup resize observer if requested
    if (opts.observeResize && typeof ResizeObserver !== 'undefined') {
      this._resizeObserver = new ResizeObserver(() => {
        this.setViewport({
          width: this._dom.clientWidth,
          height: this._dom.clientHeight
        });
      });
      this._resizeObserver.observe(this._dom);
    }
  }

  dispose() {
    if (this._animHandle != null) {
      caf(this._animHandle);
      this._animHandle = null;
    }
    if (this._easeAbort) this._easeAbort.abort();
    this._activeAnimation = null;
    this._handlers?.dispose();
    if (this._resizeObserver) {
      this._resizeObserver.disconnect();
      this._resizeObserver = undefined;
    }
    if (this._moveEndTimer != null) {
      (globalThis as any).clearTimeout?.(this._moveEndTimer);
      this._moveEndTimer = null;
    }
    this._endAllAxes();
  }

  // Internal fire method that respects event suppression
  private _fire<T extends keyof CameraMoveEvents>(type: T, data: CameraMoveEvents[T]) {
    if (this._suppressEvents || this._isInternalUpdate) {
      return; // Don't fire events
    }
    this.fire(type, data);
  }

  // Enable/disable the mobile touch debug overlay at runtime
  setTouchDebugOverlay(enabled: boolean) {
    this._handlers?.setTouchDebugOverlay?.(enabled);
  }

  setViewport(view: { width: number; height: number; devicePixelRatio?: number }) {
    this.transform.setViewport(view);
  }

  getCenter() { return this.transform.center; }
  getZoom() { return this.transform.zoom; }
  getBearing() { return this.transform.bearing; }
  getPitch() { return this.transform.pitch; }
  getRoll() { return this.transform.roll; }
  getPadding() { return this.transform.padding; }

  isMoving() { return this._moving; }
  isZooming() { return this._zooming; }
  isRotating() { return this._rotating; }
  isPitching() { return this._pitching; }
  isRolling() { return this._rolling; }

  setCenter(center: { x: number; y: number; z?: number }) { this.transform.setCenter(center); this._emitRender(); return this; }
  setZoom(zoom: number) { this.transform.setZoom(zoom); this._emitRender(); return this; }
  setBearing(bearing: number) { this.transform.setBearing(bearing); this._emitRender(); return this; }
  setPitch(pitch: number) { this.transform.setPitch(pitch); this._emitRender(); return this; }
  setRoll(roll: number) { this.transform.setRoll(roll); this._emitRender(); return this; }
  setPadding(padding: Partial<Padding>) { this.transform.setPadding(padding); this._emitRender(); return this; }
  setConstraints(c: Partial<TransformConstraints>) { this._constraints = { ...this._constraints, ...c }; this.transform.setConstraints(this._constraints); this._emitRender(); return this; }

  jumpTo(options: { center?: { x: number; y: number; z?: number }; zoom?: number; bearing?: number; pitch?: number; roll?: number; padding?: Partial<Padding> }, methodOpts?: MethodOptions) {
    const wasSuppressed = this._isInternalUpdate;
    if (methodOpts?.silent) this._isInternalUpdate = true;

    if (options.center) this.transform.setCenter(options.center);
    if (typeof options.zoom === 'number') this.transform.setZoom(options.zoom);
    if (typeof options.bearing === 'number') this.transform.setBearing(options.bearing);
    if (typeof options.pitch === 'number') this.transform.setPitch(options.pitch);
    if (typeof options.roll === 'number') this.transform.setRoll(options.roll);
    if (options.padding) this.transform.setPadding(options.padding);
    this._emitRender();

    this._isInternalUpdate = wasSuppressed;
    return this;
  }

  panBy(offset: { x: number; y: number }, _opts?: EaseOptions) {
    this._helper.handleMapControlsPan(this.transform, offset.x, offset.y);
    this._emitRender();
    return this;
  }

  panTo(center: { x: number; y: number }, opts?: EaseOptions) {
    return this.easeTo({ ...opts, center });
  }

  zoomTo(zoom: number, opts?: EaseOptions) { return this.easeTo({ ...opts, zoom }); }
  zoomIn(delta = 1, opts?: EaseOptions) { return this.zoomTo(this.getZoom() + delta, opts); }
  zoomOut(delta = 1, opts?: EaseOptions) { return this.zoomTo(this.getZoom() - delta, opts); }

  rotateTo(bearing: number, opts?: EaseOptions) { return this.easeTo({ ...opts, bearing }); }
  rotateBy(delta: number, opts?: EaseOptions) { return this.rotateTo(this.getBearing() + delta, opts); }
  pitchTo(pitch: number, opts?: EaseOptions) { return this.easeTo({ ...opts, pitch }); }
  pitchBy(delta: number, opts?: EaseOptions) { return this.pitchTo(this.getPitch() + delta, opts); }
  rollTo(roll: number, opts?: EaseOptions) { return this.easeTo({ ...opts, roll }); }
  rollBy(delta: number, opts?: EaseOptions) { return this.rollTo(this.getRoll() + delta, opts); }

  easeTo(options: { center?: { x: number; y: number; z?: number }; zoom?: number; bearing?: number; pitch?: number; roll?: number; padding?: Partial<Padding>; offset?: { x: number; y: number } } & EaseOptions) {
    // Reduced motion handling
    const essential = options.essential ?? false;
    const animate = options.animate ?? true;
    if (!essential && browser.reducedMotion()) {
      return this.jumpTo(options);
    }

    const start = {
      center: this.getCenter(),
      zoom: this.getZoom(),
      bearing: this.getBearing(),
      pitch: this.getPitch(),
      roll: this.getRoll(),
      padding: this.getPadding(),
    };
    const target = {
      center: options.center ?? start.center,
      zoom: typeof options.zoom === 'number' ? options.zoom : start.zoom,
      bearing: typeof options.bearing === 'number' ? options.bearing : start.bearing,
      pitch: typeof options.pitch === 'number' ? options.pitch : start.pitch,
      roll: typeof options.roll === 'number' ? options.roll : start.roll,
      padding: options.padding ? { ...start.padding, ...options.padding } : start.padding,
    };
    // Apply offset in screen space to adjust target center
    if (options.offset && (options.offset.x !== 0 || options.offset.y !== 0)) {
      // Apply offset in rotated screen-space relative to target bearing
      const rad = (target.bearing ?? this.getBearing()) * Math.PI / 180;
      const cos = Math.cos(rad), sin = Math.sin(rad);
      const ox = options.offset.x, oy = options.offset.y;
      const rx = ox * cos + oy * sin;
      const ry = -ox * sin + oy * cos;
      // Use cached scale if target zoom matches current zoom
      const s = target.zoom === this.getZoom() ? (this.transform as any).scale ?? Math.pow(2, target.zoom) : Math.pow(2, target.zoom);
      const dxW = -rx / s;
      const dyW = ry / s;
      target.center = { x: target.center.x + dxW, y: target.center.y + dyW, z: target.center.z };
    }

    if (!animate) {
      return this.jumpTo(target);
    }

    const duration = Math.max(0, options.duration ?? 300);
    const easing = options.easing ?? defaultEasing;
    // Axis starts (treat center/offset/around-pointer as pan involvement)
    const axes = {
      zoom: target.zoom !== start.zoom,
      rotate: target.bearing !== start.bearing,
      pitch: target.pitch !== start.pitch,
      roll: target.roll !== start.roll,
      pan: !!(options.center || options.offset || (options as any).around === 'pointer')
    };
    this._startMoveLifecycle();
    this._axisStart(axes);

    if (this._easeAbort) this._easeAbort.abort();
    this._easeAbort = new AbortController();

    const t0 = browser.now();
    const anchorPt = (options as any).aroundPoint as { x: number; y: number } | undefined;
    const useAnchor = (options as any).around === 'pointer' && !!anchorPt;
    const tight = Math.max(0, Math.min(1, (options as any).anchorTightness ?? 1));

    // Store animation state
    this._activeAnimation = {
      type: 'ease',
      t0,
      duration,
      easing,
      start,
      target,
      axes,
      anchorPt: useAnchor ? anchorPt : undefined,
      anchorTightness: useAnchor ? tight : undefined,
    };

    // If using external loop, caller will drive animation via update()
    if (this._useExternalLoop) {
      return this;
    }

    // Otherwise, use internal RAF loop
    const loop = () => {
      const continues = this._advanceAnimation(browser.now());
      if (continues) {
        this._animHandle = raf(loop);
      } else {
        this._animHandle = null;
      }
    };
    this._animHandle = raf(loop);
    return this;
  }

  flyTo(options: { center?: { x: number; y: number; z?: number }; zoom?: number; bearing?: number; pitch?: number; roll?: number; maxDuration?: number } & FlyToOptions) {
    const startCenter = this.getCenter();
    const endCenter = options.center ?? startCenter;
    const startZoom = this.getZoom();
    const endZoom = typeof options.zoom === 'number' ? options.zoom : startZoom;
    const startBearing = this.getBearing();
    const endBearing = typeof options.bearing === 'number' ? options.bearing : startBearing;
    const startPitch = this.getPitch();
    const endPitch = typeof options.pitch === 'number' ? options.pitch : startPitch;
    const startRoll = this.getRoll();
    const endRoll = typeof options.roll === 'number' ? options.roll : startRoll;

    const dx = endCenter.x - startCenter.x;
    const dy = endCenter.y - startCenter.y;
    const worldDist = Math.hypot(dx, dy);
    const pad = options.padding ?? this.getPadding();
    const effH = Math.max(1, this.transform.height - ((pad?.top ?? 0) + (pad?.bottom ?? 0)));
    const startScale = Math.pow(2, startZoom);
    const pxDist = worldDist * startScale;

    // Determine duration
    let duration = options.duration;
    if (duration == null) {
      if (options.screenSpeed && options.screenSpeed > 0) {
        duration = (pxDist / options.screenSpeed) * 1000;
      } else if (options.speed && options.speed > 0) {
        duration = (pxDist / (options.speed * 100)) * 1000;
      } else {
        duration = 1000;
      }
      if (options.maxDuration != null) duration = Math.min(duration, options.maxDuration);
    }

    // If screenSpeed provided, run a constant-pixel-speed center motion with eased zoom/angles
    const useScreenSpeed = !!options.screenSpeed && options.screenSpeed! > 0 && pxDist > 0;
    if (!useScreenSpeed) {
      // Van Wijk & Nuij-like path with hyperbolic functions
      const rho = Math.max(0.01, Math.min(5, options.curve ?? 1.42));
      const u1 = pxDist; // pixel ground distance at start scale
      // Use effective viewport height as visible span including padding
      const w0 = effH;
      const w1 = w0 * (Math.pow(2, startZoom) / Math.pow(2, endZoom));
      if (u1 < 1e-3) {
        return this.easeTo({ ...options, duration });
      }
      const params = computeFlyParams(w0, w1, u1, rho);
      const S = params.S;
      // If duration not provided, derive from S and speed
      if (options.duration == null) {
        const v = options.speed && options.speed > 0 ? options.speed : 1.2;
        duration = (Math.abs(S) / v) * 1000;
      }
      // Align durations across axes: ensure rotation/pitch/roll can complete
      const degSpeed = 180; // deg/sec baseline
      const pitchSpeed = 120; // deg/sec baseline
      const rollSpeed = 180; // deg/sec baseline
      const rotDur = Math.abs(endBearing - startBearing) / degSpeed * 1000;
      const pitDur = Math.abs(endPitch - startPitch) / pitchSpeed * 1000;
      const rolDur = Math.abs(endRoll - startRoll) / rollSpeed * 1000;
      duration = Math.max(duration!, rotDur, pitDur, rolDur);
      if (options.maxDuration != null) duration = Math.min(duration, options.maxDuration);
      const easing = options.easing ?? defaultEasing;
      const axes = { zoom: endZoom !== startZoom, rotate: endBearing !== startBearing, pitch: endPitch !== startPitch, roll: endRoll !== startRoll, pan: worldDist > 0 };
      this._startMoveLifecycle();
      this._axisStart(axes);
      if (this._easeAbort) this._easeAbort.abort();
      this._easeAbort = new AbortController();

      const t0 = browser.now();

      // Store animation state
      this._activeAnimation = {
        type: 'fly',
        t0,
        duration: duration!,
        easing,
        start: { center: startCenter, zoom: startZoom, bearing: startBearing, pitch: startPitch, roll: startRoll, padding: this.getPadding() },
        target: { center: endCenter, zoom: endZoom, bearing: endBearing, pitch: endPitch, roll: endRoll, padding: this.getPadding() },
        axes,
        flyParams: {
          useScreenSpeed: false,
          S,
          w0,
          w1,
          u1,
          rho,
          params,
          dirX: 0,
          dirY: 0,
          startCenter,
          endCenter,
          startZoom,
          endZoom,
          startBearing,
          endBearing,
          startPitch,
          endPitch,
          startRoll,
          endRoll,
          worldDist,
        },
      };

      // If using external loop, caller will drive animation via update()
      if (this._useExternalLoop) {
        return this;
      }

      // Otherwise, use internal RAF loop
      const loop = () => {
        const continues = this._advanceAnimation(browser.now());
        if (continues) {
          this._animHandle = raf(loop);
        } else {
          this._animHandle = null;
        }
      };
      this._animHandle = raf(loop);
      return this;
    }

    const easing = options.easing ?? defaultEasing;
    const axes = { zoom: endZoom !== startZoom, rotate: endBearing !== startBearing, pitch: endPitch !== startPitch, roll: endRoll !== startRoll, pan: worldDist > 0 };
    this._startMoveLifecycle();
    this._axisStart(axes);
    if (this._easeAbort) this._easeAbort.abort();
    this._easeAbort = new AbortController();

    const t0 = browser.now();
    const dirX = worldDist > 0 ? dx / worldDist : 0;
    const dirY = worldDist > 0 ? dy / worldDist : 0;

    // Store animation state
    this._activeAnimation = {
      type: 'fly',
      t0,
      duration: duration!,
      easing,
      start: { center: startCenter, zoom: startZoom, bearing: startBearing, pitch: startPitch, roll: startRoll, padding: this.getPadding() },
      target: { center: endCenter, zoom: endZoom, bearing: endBearing, pitch: endPitch, roll: endRoll, padding: this.getPadding() },
      axes,
      flyParams: {
        useScreenSpeed: true,
        screenSpeed: options.screenSpeed,
        S: 0, // not used for screenSpeed path
        w0: 0,
        w1: 0,
        u1: 0,
        rho: 0,
        params: null,
        dirX,
        dirY,
        traveled: 0,
        last: t0,
        startCenter,
        endCenter,
        startZoom,
        endZoom,
        startBearing,
        endBearing,
        startPitch,
        endPitch,
        startRoll,
        endRoll,
        worldDist,
      },
    };

    // If using external loop, caller will drive animation via update()
    if (this._useExternalLoop) {
      return this;
    }

    // Otherwise, use internal RAF loop
    const loop = () => {
      const continues = this._advanceAnimation(browser.now());
      if (continues) {
        this._animHandle = raf(loop);
      } else {
        this._animHandle = null;
      }
    };
    this._animHandle = raf(loop);
    return this;
  }

  fitBounds(bounds: { min: { x: number; y: number }; max: { x: number; y: number } }, options?: EaseOptions & { offset?: { x: number; y: number } }) {
    const { center, zoom, bearing } = new PlanarCameraHelper().cameraForBoxAndBearing(this.transform, bounds, options);
    return this.easeTo({ center, zoom, bearing, ...options });
  }

  cameraForBounds(bounds: { min: { x: number; y: number }; max: { x: number; y: number } }, options?: EaseOptions & { offset?: { x: number; y: number } }) {
    return new PlanarCameraHelper().cameraForBoxAndBearing(this.transform, bounds, options);
  }

  // State snapshot methods for programmatic control without events
  getStateSnapshot() {
    return {
      center: this.getCenter(),
      zoom: this.getZoom(),
      bearing: this.getBearing(),
      pitch: this.getPitch(),
      roll: this.getRoll(),
      padding: this.getPadding()
    };
  }

  setStateSnapshot(state: {
    center?: { x: number; y: number; z?: number };
    zoom?: number;
    bearing?: number;
    pitch?: number;
    roll?: number;
    padding?: Partial<Padding>;
  }, methodOpts?: MethodOptions) {
    return this.jumpTo(state, methodOpts);
  }

  /**
   * Update animation state (for external animation loops).
   * Call this from your animation loop (e.g., React Three Fiber's useFrame) when
   * useExternalAnimationLoop is true. This advances any active easeTo/flyTo animation.
   *
   * @param deltaTime - Optional delta time in seconds (currently unused, for future use)
   * @returns this for chaining
   */
  update(deltaTime?: number): this {
    if (this._activeAnimation) {
      this._advanceAnimation(browser.now());
    }
    return this;
  }

  /**
   * Advance the active animation by one frame.
   * Returns true if animation continues, false if complete.
   * @internal
   */
  private _advanceAnimation(now: number): boolean {
    if (!this._activeAnimation) return false;

    const anim = this._activeAnimation;
    const k = Math.min(1, (now - anim.t0) / anim.duration);
    const e = anim.easing(k);

    if (anim.type === 'ease') {
      // easeTo animation
      const groundBefore = anim.anchorPt ? this.transform.groundFromScreen(anim.anchorPt) : null;

      this.transform.deferApply(() => {
        const startZ = anim.start.center.z ?? 0;
        const targetZ = anim.target.center.z ?? 0;
        this.transform.setCenter({
          x: anim.start.center.x + (anim.target.center.x - anim.start.center.x) * e,
          y: anim.start.center.y + (anim.target.center.y - anim.start.center.y) * e,
          z: startZ + (targetZ - startZ) * e,
        });
        this.transform.setZoom(anim.start.zoom + (anim.target.zoom - anim.start.zoom) * e);
        this.transform.setBearing(anim.start.bearing + (anim.target.bearing - anim.start.bearing) * e);
        this.transform.setPitch(anim.start.pitch + (anim.target.pitch - anim.start.pitch) * e);
        this.transform.setRoll(anim.start.roll + (anim.target.roll - anim.start.roll) * e);
        this.transform.setPadding({
          top: anim.start.padding.top + (anim.target.padding.top - anim.start.padding.top) * e,
          right: anim.start.padding.right + (anim.target.padding.right - anim.start.padding.right) * e,
          bottom: anim.start.padding.bottom + (anim.target.padding.bottom - anim.start.padding.bottom) * e,
          left: anim.start.padding.left + (anim.target.padding.left - anim.start.padding.left) * e,
        });
      });

      if (groundBefore && anim.anchorPt) {
        const groundAfter = this.transform.groundFromScreen(anim.anchorPt);
        if (groundAfter) {
          const tight = anim.anchorTightness ?? 1;
          const dgx = (groundBefore.gx - groundAfter.gx) * tight;
          const dgz = (groundBefore.gz - groundAfter.gz) * tight;
          if (dgx || dgz) this.transform.adjustCenterByGroundDelta(dgx, dgz);
        }
      }

      this._axisEmitDuring(anim.axes);
      this._emitRender();

      if (k < 1 && this._easeAbort && !this._easeAbort.signal.aborted) {
        return true; // Continue
      } else {
        // Complete
        if (anim.axes.rotate) this._applyBearingSnap();
        this._applySoftPanBounds();
        this._axisEnd(anim.axes);
        this._endMoveLifecycle();
        this._activeAnimation = null;
        return false;
      }
    } else if (anim.type === 'fly' && anim.flyParams) {
      // flyTo animation
      const fp = anim.flyParams;

      if (fp.useScreenSpeed) {
        // Constant screen speed path
        const z = fp.startZoom + (fp.endZoom - fp.startZoom) * e;
        const b = fp.startBearing + (fp.endBearing - fp.startBearing) * e;
        const p = fp.startPitch + (fp.endPitch - fp.startPitch) * e;
        const r = fp.startRoll + (fp.endRoll - fp.startRoll) * e;
        this.transform.deferApply(() => {
          this.transform.setZoom(z);
          this.transform.setBearing(b);
          this.transform.setPitch(p);
          this.transform.setRoll(r);
        });

        // Constant screen speed center motion
        const dt = (now - (fp.last ?? anim.t0)) / 1000;
        fp.last = now;
        const scale = Math.pow(2, z);
        const stepWorld = ((fp.screenSpeed ?? 0) * dt) / scale;
        const traveled = Math.min(fp.worldDist, (fp.traveled ?? 0) + stepWorld);
        fp.traveled = traveled;
        const cx = fp.startCenter.x + fp.dirX * traveled;
        const cy = fp.startCenter.y + fp.dirY * traveled;
        this.transform.setCenter({ x: cx, y: cy, z: fp.startCenter.z });

        this._axisEmitDuring(anim.axes);
        this._emitRender();

        if (k < 1 && traveled < fp.worldDist && this._easeAbort && !this._easeAbort.signal.aborted) {
          return true; // Continue
        } else {
          // Ensure final state
          this.transform.deferApply(() => {
            this.transform.setZoom(fp.endZoom);
            this.transform.setBearing(fp.endBearing);
            this.transform.setPitch(fp.endPitch);
            this.transform.setRoll(fp.endRoll);
            this.transform.setCenter({ x: fp.endCenter.x, y: fp.endCenter.y, z: fp.endCenter.z ?? fp.startCenter.z });
          });
          if (fp.endBearing !== fp.startBearing) this._applyBearingSnap();
          this._applySoftPanBounds();
          this._axisEnd(anim.axes);
          this._endMoveLifecycle();
          this._activeAnimation = null;
          return false;
        }
      } else {
        // Van Wijk path
        const s = fp.S * e;
        const w = widthAt(fp.params, s);
        const scaleRatio = fp.w0 / w;
        const z = fp.startZoom + Math.log2(scaleRatio);
        const u = uAt(fp.params, s);
        const fu = Math.max(0, Math.min(1, u / fp.u1));
        const cx = fp.startCenter.x + (fp.endCenter.x - fp.startCenter.x) * (fu / (fp.worldDist || 1));
        const cy = fp.startCenter.y + (fp.endCenter.y - fp.startCenter.y) * (fu / (fp.worldDist || 1));
        const b = fp.startBearing + (fp.endBearing - fp.startBearing) * e;
        const p = fp.startPitch + (fp.endPitch - fp.startPitch) * e;
        const rr = fp.startRoll + (fp.endRoll - fp.startRoll) * e;

        this.transform.setZoom(z);
        this.transform.setBearing(b);
        this.transform.setPitch(p);
        this.transform.setRoll(rr);
        this.transform.setCenter({ x: cx, y: cy, z: fp.startCenter.z });

        this._axisEmitDuring(anim.axes);
        this._emitRender();

        if (k < 1 && this._easeAbort && !this._easeAbort.signal.aborted) {
          return true; // Continue
        } else {
          this.transform.deferApply(() => {
            this.transform.setZoom(fp.endZoom);
            this.transform.setBearing(fp.endBearing);
            this.transform.setPitch(fp.endPitch);
            this.transform.setRoll(fp.endRoll);
            this.transform.setCenter({ x: fp.endCenter.x, y: fp.endCenter.y, z: fp.endCenter.z ?? fp.startCenter.z });
          });
          if (fp.endBearing !== fp.startBearing) this._applyBearingSnap();
          this._applySoftPanBounds();
          this._axisEnd(anim.axes);
          this._endMoveLifecycle();
          this._activeAnimation = null;
          return false;
        }
      }
    }

    return false;
  }

  private _emitRender() {
    this._fire('renderFrame', {});
  }

  private _startMoveLifecycle() {
    if (!this._moving) {
      this._moving = true;
      this._fire('movestart', {});
    }
    this._fire('move', {});
  }

  private _endMoveLifecycle() {
    if (this._moving) {
      this._moving = false;
      this._fire('moveend', {});
    }
  }

  private _externalChange(delta?: { axes?: { pan?: boolean; zoom?: boolean; rotate?: boolean; pitch?: boolean; roll?: boolean }; originalEvent?: Event }) {
    const axes = delta?.axes ?? {};
    this._startMoveLifecycle();
    this._axisStart(axes, delta?.originalEvent);
    this._axisEmitDuring(axes, delta?.originalEvent);
    this._emitRender();
    if (this._moveEndTimer != null) {
      (globalThis as any).clearTimeout?.(this._moveEndTimer);
    }
    // Debounce moveend after burst of external changes (SSR-safe timers)
    this._moveEndTimer = (globalThis as any).setTimeout?.(() => {
      if (axes.rotate) this._applyBearingSnap(delta?.originalEvent);
      this._applySoftPanBounds();
      this._axisEnd(axes, delta?.originalEvent);
      this._endMoveLifecycle();
      this._moveEndTimer = null;
    }, 120) as any;
  }

  private _axisStart(axes: { zoom?: boolean; rotate?: boolean; pitch?: boolean; roll?: boolean; pan?: boolean }, originalEvent?: Event) {
    if (axes.zoom && !this._zooming) { this._zooming = true; this._fire('zoomstart', { originalEvent }); }
    if (axes.rotate && !this._rotating) { this._rotating = true; this._fire('rotatestart', { originalEvent }); }
    if (axes.pitch && !this._pitching) { this._pitching = true; this._fire('pitchstart', { originalEvent }); }
    if (axes.roll && !this._rolling) { this._rolling = true; this._fire('rollstart', { originalEvent }); }
    if (axes.pan && !this._dragging) { this._dragging = true; this._fire('dragstart', { originalEvent }); }
  }

  private _axisEmitDuring(axes: { zoom?: boolean; rotate?: boolean; pitch?: boolean; roll?: boolean; pan?: boolean }, originalEvent?: Event) {
    if (axes.zoom) this._fire('zoom', { originalEvent });
    if (axes.rotate) this._fire('rotate', { originalEvent });
    if (axes.pitch) this._fire('pitch', { originalEvent });
    if (axes.roll) this._fire('roll', { originalEvent });
    if (axes.pan) this._fire('drag', { originalEvent });
  }

  private _axisEnd(axes: { zoom?: boolean; rotate?: boolean; pitch?: boolean; roll?: boolean; pan?: boolean }, originalEvent?: Event) {
    if (axes.zoom && this._zooming) { this._zooming = false; this._fire('zoomend', { originalEvent }); }
    if (axes.rotate && this._rotating) { this._rotating = false; this._fire('rotateend', { originalEvent }); }
    if (axes.pitch && this._pitching) { this._pitching = false; this._fire('pitchend', { originalEvent }); }
    if (axes.roll && this._rolling) { this._rolling = false; this._fire('rollend', { originalEvent }); }
    if (axes.pan && this._dragging) { this._dragging = false; this._fire('dragend', { originalEvent }); }
  }

  private _endAllAxes() {
    if (this._zooming) { this._zooming = false; this._fire('zoomend', {}); }
    if (this._rotating) { this._applyBearingSnap(); this._rotating = false; this._fire('rotateend', {}); }
    if (this._pitching) { this._pitching = false; this._fire('pitchend', {}); }
    if (this._rolling) { this._rolling = false; this._fire('rollend', {}); }
    if (this._dragging) { this._dragging = false; this._fire('dragend', {}); }
  }

  private _applyBearingSnap(originalEvent?: Event) {
    const snap = this._bearingSnap;
    if (snap > 0) {
      const b = this.getBearing();
      if (Math.abs(b) <= snap + this._bearingSnapEps) {
        this.transform.setBearing(0);
        this._fire('rotate', { originalEvent });
        this._emitRender();
      }
    }
  }

  private _applySoftPanBounds() {
    if (this._softClamping) return;
    const bounds = this._constraints.panBounds;
    if (!bounds) return;
    const c = this.getCenter();
    const clamped = {
      x: Math.max(bounds.min.x, Math.min(bounds.max.x, c.x)),
      y: Math.max(bounds.min.y, Math.min(bounds.max.y, c.y)),
      z: c.z,
    };
    if (clamped.x !== c.x || clamped.y !== c.y) {
      this._softClamping = true;
      // Smooth nudge back to bounds
      this.easeTo({ center: clamped, duration: 180, easing: defaultEasing, essential: true });
      // Clear flag after short delay to avoid recursion (SSR-safe)
      (globalThis as any).setTimeout?.(() => { this._softClamping = false; }, 220);
    }
  }
}
